import { Component, OnInit, Inject } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent  implements OnInit{

  title = 'getMyApp';
  downloadLink='https://drive.google.com/uc?export=download&id=12NqlIF_ESfK4gm0JWpd4N6FNk1PyQFTC';

  constructor(private http: HttpClient,public dialog: MatDialog){
    
  }

  ngOnInit(){
    this.getLink();
  }

  contactUs(){
    // console.log("clicked");
    let emailCC="soumya.c11@gmail.com";
    let email="geturapponline@gmail.com";
    let emailSub="Get My App Catalogue request"
      window.open('mailto:'+email+'?Subject='+emailSub, '_blank');
  }

  openDemo(){
    window.open(this.downloadLink);
    // this.getLink();
  }
  
  getLink(){
    this.http.get("https://osm-server.herokuapp.com/values/getAll/getMyAppDemoLink").subscribe((values:Array<any>)=>{
      if(values.length>0 && values[0].value1.length>0){
        this.downloadLink=values[0].value1;
      }
    })
  }

  openDialog(): void {
    const dialogRef = this.dialog.open(DialogOverviewExampleDialog, {
      width: '250px',
    });

    dialogRef.afterClosed().subscribe(result => {
      if(result == "download"){
        this.openDemo();
      }
    });
  }


}



@Component({
  selector: 'demo-dialog',
  templateUrl: 'demo-dialog.html',
  styleUrls: ['./demo-dialog.scss'],
})
export class DialogOverviewExampleDialog {

  constructor(
    public dialogRef: MatDialogRef<DialogOverviewExampleDialog>,
    @Inject(MAT_DIALOG_DATA) public data:any) {}

  onNoClick(): void {
    this.dialogRef.close();
  }

}